/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { useStaticQuery, graphql } from 'gatsby';
import SimplePostItem from 'components/Items/SimplePostItem';
import { Post } from 'models/post';
import WidgetCard from 'components/Cards/WidgetCard';
import { useCallback } from 'react';
import { linkEvent } from 'utils/loggers';

const RecentPostsCard = () => {
  const data = useStaticQuery(graphql`
    query RecentPostsQuery {
      allMarkdownRemark(limit: 5, sort: { order: DESC, fields: frontmatter___date }) {
        edges {
          node {
            fields {
              path
            }
            frontmatter {
              title
              categories
              date(formatString: "YYYY-MM-DD")
              thumbnail {
                childImageSharp {
                  fixed(width: 64, height: 64) {
                    src
                  }
                }
              }
            }
          }
        }
      }
    }
  `);
  const {
    allMarkdownRemark: { edges },
  } = data;

  const handleRecentPostClick = useCallback((url: string) => {
    linkEvent(url, '최근 글 링크 클릭');
  }, []);

  return (
    <WidgetCard title="최근 글">
      <ul
        css={css`
          margin: 0;
          padding: 0;
        `}
      >
        {edges
          .map(e => e.node)
          .map((post: Post, index) => (
            <SimplePostItem
              key={post.fields.path ?? index}
              thumbnail={post.frontmatter.thumbnail?.childImageSharp?.fixed?.src ?? ''}
              title={post.frontmatter.title ?? ''}
              date={post.frontmatter.date}
              path={post.fields.path ?? '/'}
              categories={post.frontmatter.categories}
              onClick={handleRecentPostClick}
            />
          ))}
      </ul>
    </WidgetCard>
  );
};

export default RecentPostsCard;
